/************************************************************/
@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");
#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;

  flex-direction: column;
}

:root {
  --almost-white: hsl(0, 0%, 98%);
  --medium-gray: hsl(0, 0%, 41%);
  --almost-black: hsl(0, 0%, 8%);
}
.container {
  max-width: 1470px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;
}
main {
  flex: 1 1 100%;
}
body {
  font-family: Inter;
  font-size: 18px;
  color: var(--almost-black);
  font-family: Epilogue;
  font-weight: 500;
}

// header styles
.header {
  padding-top: 2rem;
  position: fixed;
  width: 100%;

  z-index: 6;
  background: var(--almost-white);
}

.header-body {
  display: flex;
  width: 100%;

  justify-content: space-between;
  align-items: center;
}
.header-logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-menu {
  width: 100%;
  height: 100%;
  max-width: 40%;
  position: relative;
  @media (max-width: 767px) {
    max-height: 60%;
  }
}
.mobileMenu {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
}
.headerMenuContent {
  width: 100%;
  display: flex;

  height: 100%;
  align-items: center;
  justify-content: flex-end;

  @media (max-width: 767px) {
    flex-direction: column;
    justify-content: center;
    position: fixed;
    align-items: flex-start;
    background-color: var(--almost-white);

    top: 0;
    max-height: 100%;
    right: -100%;
    transition: all 0.6s ease 0s;
    &.activer {
      right: 0;
    }

    max-width: 250px;
    padding-top: 10%;
    padding-left: 3rem;
    padding-bottom: 3rem;
    z-index: 5;
  }
}
.header-list {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  @media (max-width: 767px) {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    max-height: 250px;
  }
}

.header-li {
  &:not(:last-child) {
    margin-right: 1rem;
    @media (max-width: 767px) {
      margin-bottom: 1rem;
    }
  }
}
.icon::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 10px;

  top: 7px;
  right: -12px;
  background: url("./components/images/icon-arrow-down.svg") no-repeat;
  background-size: 100%;
  transition: all 0.3s;
}
.icon:hover::after {
  transform: rotate(180deg);
}
.header-link {
  display: inline-block;
  padding: 3px;

  font-size: inherit;
  color: var(--almost-black);
  position: relative;
  cursor: pointer;

  &:hover {
    color: var(--medium-gray);
    .header-SubList {
      opacity: 1;
      height: 100%;
      @media (max-width: 767px) {
        display: flex;
      }
    }
  }
  .header-SubList {
    width: 150px;
    height: 200px;
    max-height: 150px;
    opacity: 0;
    height: 0;
    transition: all 0.3s ease-in-out 0s;
    background-color: var(--almost-white);

    padding: 1rem;
    display: flex;
    @media (max-width: 767px) {
      display: none;
    }
    gap: 1rem;

    @media (min-width: 767px) {
      position: fixed;
      justify-content: center;
      align-items: center;
    }
    top: 10%;

    flex-direction: column;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.24);
    border-radius: 15px;
  }
}

.header-subLi a {
  color: var(--almost-black);
  display: flex;

  gap: 10px;
  @media (max-width: 767px) {
    padding: 3px;
  }
  &:hover {
    color: var(--medium-gray);
  }
}
.header-buttons {
  display: flex;

  gap: 1rem;
  @media (min-width: 767px) {
    padding-left: 25%;
  }
  @media (max-width: 767px) {
    flex-direction: column;
  }
}
.header-login {
  color: var(--almost-black);
  border-radius: 10px;
  padding: 1rem;
  transition: all 0.3s;
  &:hover {
    color: var(--medium-gray);
  }
}
.header-register {
  transition: all 0.3s;
  color: var(--almost-black);
  &:hover {
    color: var(--medium-gray);
  }
  padding: 1rem;
  border: solid 1px var(--almost-black);
  border-radius: 15px;
}
.burger {
  cursor: pointer;
  z-index: 5;
  font-size: 24px;
}

.page {
  display: flex;
  @media (max-width: 767px) {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
}
.page-body {
  flex: 0 1 36%;
  display: flex;
  flex-direction: column;
  @media (min-width: 968px) {
    padding: 20% 7% 7% 7%;
  }
  @media (max-width: 967px) {
    padding: 20% 3% 3% 3%;
  }
  @media (max-width: 767px) {
    padding: 1%;
    align-items: center;
    justify-content: center;
  }
}
.page-mainImg {
  flex: 1 1 50%;
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: 100%;
  margin-bottom: 10%;

  img {
    width: 100%;
    height: 100%;
    position: absolute;
    @media (min-width: 767px) {
      top: -10%;
    }
    @media (max-width: 524px) {
      top: 10%;
    }
    left: 0;
    object-fit: contain;
  }
}
.page-title {
  font-size: 52px;
  font-weight: 700;
  margin-bottom: 10%;
  @media (max-width: 524px) {
    font-size: 40px;
    text-align: center;
  }
}
.page-text {
  color: var(--medium-gray);
  margin-bottom: 10%;
  max-width: 100%;
  line-height: 140%;
  @media (max-width: 524px) {
    text-align: center;
  }
}
.page-button {
  max-width: 150px;
  margin-bottom: 10%;
  display: inline-block;
  cursor: pointer;
  padding: 1rem 1.5rem;
  color: var(--almost-white);
  background-color: var(--almost-black);
  border-radius: 20px;
  transition: all 0.3s ease 0s;
  border: solid 1px var(--almost-black);
  &:hover {
    background-color: var(--almost-white);
    color: var(--almost-black);
  }
}
.page-icons {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 10%;
  @media (max-width: 524px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.active::after {
  content: "";
  background: rgba(0, 0, 0, 0.692);
  width: 100%;
  height: 1000px;
  top: 0;
  left: 0;
  position: absolute;
}
